=========================================================*/

/*[ Effect1 ]
-----------------------------------------------------------
*/
.effect1 {
  position: relative;
  border-bottom: 1px solid #cccccc;
}
.effect1-line {
  display: block;
  position: absolute;
  width: 0;
  height: 1px;
  background-color: #e65540;
  left: 0;
  bottom: -1px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.effect1 input:focus + .effect1-line {
  width: 100%;
}

/*[ Effect2 ]
-----------------------------------------------------------
*/
.eff2:active {
  background-color: #e65540;
  color: white;
}

.eff3 {
  opacity: 0.7;
}
.eff3:hover {
  opacity: 1;
}

