}

.hov6:hover {
  color: white;
  text-decoration: underline;
}

/*[ Active ]
///////////////////////////////////////////////////////////
*/
.active1 {
  color: #333333;
}

/*[ Parallax ]
///////////////////////////////////////////////////////////
*/
.parallax0 {
  background-attachment: fixed;
  background-position: center 0;
  background-repeat: no-repeat;
  background-size: cover;
}

.overlay0 {
