.container-table-cart::before {
  display: block;
  position: absolute;
  width: 1px;
  height: calc(100% - 51px);
  background-color: #e7e6e4;
  top: 551px;
  left: 0;
}

.container-table-cart::after {
  content: "";
  display: block;
  position: absolute;
  width: 1px;
  height: calc(100% - 51px);
  background-color: #e7e6e4;
  top: 51px;
  right: 0;
}

.table-shopping-cart {
